import { Box, Typography, styled } from '@mui/material'
import React from 'react'
import CloseFullscreenIcon from '@mui/icons-material/CloseFullscreen';
import {Controlled as ControlledEditor} from 'react-codemirror2'
import 'codemirror/lib/codemirror.css';
import 'codemirror/theme/material.css';
import 'codemirror/mode/xml/xml';
import 'codemirror/mode/javascript/javascript';
import 'codemirror/mode/css/css';
import '../App.css';
import { useState } from 'react';

const Container = styled(Box)`
    background:#060606;
    flex-grow: 1;
    flex-basic: 0;
    display: flex;
    flex-direction: column;
    padding: 0 3px 15px;
`

export default function Editor({heading, sign, color, value, onchange}) {
  const codeChange = (editor, data, value) =>{
    onchange(value);
  }
  const [ expand, setExpand ] = useState(true);
  return (
    <Container style={ expand ? null : {flexGrow : 0}} >
      <Box style={{margin:'0 5px 0 10px', border:'1px solid #2f2f2f'}}>
      <Box style={{background:'#060606', display:'flex'}} justifyContent='space-between' fontWeight='700px'>
        <Box style={{background:'#1d1e22', display:'flex', padding:'9px 12px', borderTop :'3px solid #1d1e22'}}>
          <Box component='span' display='flex' 
          style={{background: color, height:20, width:20, placeContent:'center', verticalAlign:'middle', 
          borderRadius:'5px', margin:'2px 4px 2px 2px', paddingBottom:'2px'}} 
          color={'#00000'}>
            {sign}
          </Box>
          <Typography variant="p" component="div" sx={{ flexGrow: 1 }} color='white'>
            {heading}
          </Typography>
        </Box>
        <CloseFullscreenIcon 
        style={{fill:'#ffffff', margin:'10px', 
        fontSize:'medium', alignSelf:'center'}}
        onClick={() => setExpand(prevState => !prevState)} />
      </Box>
      <ControlledEditor className='controlled-editor'
      onBeforeChange={codeChange}
      value={value}
      options={{
        lineNumbers:true,
        theme:'material'
      }}/>
    </Box>
    </Container>
    
  )
}
