import React from 'react'
import Editor from './Editor'
import { Box } from '@mui/material'
import { useContext } from 'react'
import { DataContext } from '../context/Dataprovide'

export default function Code() {
  const {html, setHtml, css, setCss, js, setJs} = useContext(DataContext);
  return (
    <Box style={{display:'flex'}}>
        <Editor heading="HTML" sign="/" color="#ff3c41" value={html} onchange={setHtml} />
        <Editor heading="CSS" sign="*" color="#0ebeff" value={css} onchange={setCss}/>
        <Editor heading="JavaScript" sign="()" color="#fcd000" value={js} onchange={setJs}/>
    </Box>
  )
}
