import React from 'react'
import { AppBar, Toolbar, Button, Typography } from '@mui/material';
import CloudIcon from '@mui/icons-material/Cloud';
import SettingsIcon from '@mui/icons-material/Settings';
import GroupIcon from '@mui/icons-material/Group';


export default function Header() {
        const logo = 'data:image/png;base64,';
  return (
    <>
    <AppBar style={{background : '#060606', height:'9vh', position:'static', borderBottom: '1px solid #2f2f2f'}}>
      <Toolbar>
        <img src= {logo} alt='pic' style={{ height:'5vh', width: '40', margin:'7px'}}/>
        <Typography variant="h5" component="div" sx={{ flexGrow: 1 }}>
            Codepen
          </Typography>
          <div style={{background:'#566573', margin:'2px 5px 0 5px', borderRadius:'5px', display:'flex', alignItems:'center', verticalAlign:'middle', position:'relative'}}>
          <CloudIcon style={{margin:'0 0 0 7px'}}/>
          <Button color='inherit'>Save</Button>
          </div>
          <div style={{background:'#566573', margin:'2px 5px 0 5px', borderRadius:'5px', display:'flex', alignItems:'center', verticalAlign:'middle', position:'relative'}}>
          <SettingsIcon style={{margin:'0 0 0 7px'}}/>
          <Button color='inherit'>Setting</Button>
          </div>
          <div style={{background:'#566573', margin:'2px 5px 0 5px', borderRadius:'5px', display:'flex', alignItems:'center', verticalAlign:'middle', position:'relative'}}>
          <GroupIcon style={{margin:'0 0 0 7px'}}/>
          <Button color='inherit'>Profile</Button>
          </div>
      </Toolbar>
    </AppBar>
    </>
  )
}

